.section-page {
	padding-top: 50px;
	padding-bottom: 50px;
}

.section-title {
	font-family: var(--font-family-secondary);
	font-weight: 700;
	font-size: 48px;
	color: var(--color-primary);

	span {
		display: block;
	}
}
.acor-container {
	display: grid;
	grid-template-columns: 1fr;
	grid-template-rows: auto;
	grid-row-gap: 50px;
	margin-top: 90px;
}
.acor-container .acor-head {
	cursor: pointer;
	display: block;
	padding-top: 3px;
	padding-bottom: 20px;
	width: 100%;
	box-sizing: border-box;
	z-index: 100;
	color: var(--color-third);
	letter-spacing: 0.01em;
	border-bottom: 1px solid #c1c1c1;
	transition: color 0.3s ease;
}
.acor-container label:hover {
	color: var(--color-primary-dark);
}

.acor-container .acor-head:hover .icon-arrow {
	fill: var(--color-primary-dark);
}

.acor__grid {
	display: grid;
	grid-template-columns: 1.5fr 1fr;
	grid-template-rows: auto;
}

.category-group {
	display: flex;
	align-items: center;
	margin-bottom: 30px;
}
.category-group span:not(:last-child) {
	margin-right: 15px;
}

.category-group span {
	display: flex;
	align-items: center;
	justify-content: center;
}

.category__retraining,
.category__sports {
	font-size: 13px;
	letter-spacing: 0.01em;
	padding: 2px 15px;
	border: 1px solid var(--color-third);
	border-radius: 15px;
	transition: border 0.3s ease;
}

.acor-container .acor-head:hover .category__retraining,
.acor-container .acor-head:hover .category__sports {
	border: 1px solid var(--color-primary-dark);
}

.acor__title {
	font-weight: 700;
	font-size: 32px;
	margin-bottom: 15px;
}

.acor__desc {
	max-width: 450px;
	margin-bottom: 20px;
}

.acor__desc,
.acor__duration,
.training-form {
	font-weight: 500;
	font-size: 16px;
}

.training-form {
	text-align: center;
}

.acor__column-right {
	display: flex;
	align-items: center;
	justify-content: flex-end;
}

.group__training-form {
	height: 100%;
	display: flex;
	flex-direction: column;
	margin-right: 30px;
}

.training-form__price {
	flex: 1 1 auto;
	display: flex;
	align-items: center;
	font-weight: 700;
	font-size: 24px;
	white-space: nowrap;
}

.acor__img {
	flex: 0 0 auto;
	border-radius: 50%;
	width: 150px;
	height: 150px;
	overflow: hidden;
}

.acor__arrow {
	display: flex;
	align-items: center;
	justify-content: center;
	flex: 0 0 auto;
	width: 66px;
	height: 66px;
}

.icon-arrow {
	width: 40px;
	height: 22px;
}

.acor-content .acor-body {
	width: calc(100% - 40px);
	margin: 0 auto;
	height: 0;
	color: transparent;
	background-color: var(--color-primary);
	padding: 0 65px;
	box-sizing: border-box;
	transition: color 0.5s, padding 0.5s;
	overflow: hidden;
	border-radius: 20px;
	transition: all 0.3s ease;
}

.acor-content input {
	display: none;
}

.acor-content input:checked + .acor-head .icon-arrow {
	transform: rotate(180deg);
	transition: all 0.35s ease;
}
.acor-content input + .acor-head .icon-arrow {
	// transform: rotate(90deg);
	transition: all 0.35s ease;
}
.acor-content input:checked + label + .acor-body {
	height: auto;
	margin-top: 20px;
	color: var(--color-secondary);
	padding: 60px 65px;
}
.gen-desc {
	margin-bottom: 25px;
}
.gen-desc li {
	margin-bottom: 15px;
}
.gen-modules li {
	margin-bottom: 25px;
}

.gen-desc__title {
	font-weight: 700;
	font-size: 24px;
	letter-spacing: 0.01em;
}

.gen-desc__text {
	font-size: 20px;
	letter-spacing: 0.01em;
}
