.welcome {
	padding-top: 215px;
	background: linear-gradient(180deg, #0060bf 0%, rgba(0, 96, 191, 0.78) 100%);
}
.welcome__wrapper {
	max-width: calc(var(--container-width) + 150px);
	margin: 0 auto;
	background-image: url("../img/image-bg.png");
	background-image: image-set(url("../img/image-bg.webp"));
	background-size: 525px 525px;
	background-repeat: no-repeat;
	background-position: right bottom;
	color: var(--color-secondary);
}

.page-title {
	max-width: 365px;
	font-weight: 300;
	font-size: 16px;
	line-height: 1.5;
	margin-bottom: 85px;
}

.page-desc {
	font-family: var(--font-family-secondary);
	font-weight: 700;
	font-size: 64px;
	letter-spacing: 0.06em;
	margin-bottom: 95px;
}

.welcome__link {
	font-family: inherit;
	font-weight: 500;
	font-size: 24px;
	border: 2px solid var(--color-secondary);
	border-radius: 15px;
	padding: 5px 20px;
	margin-bottom: 165px;
	background-color: transparent;
	transition: all 0.3s ease;

	&:hover {
		background-color: var(--color-secondary);
		color: var(--color-primary);
	}
}
