.header-page {
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	z-index: 42;
	background-color: transparent;
	padding-top: 5px;
}

.header-page__wrapper {
	display: flex;
	align-items: center;
	justify-content: space-between;
	height: 100px;
}
.header-page__logo {
	width: 90px;
	height: 90px;
}
.logo {
}
.logo__link {
}
.header-page__menu {
	display: flex;
	align-items: center;
	height: 100%;
	border-bottom: 1px solid var(--color-secondary);
	padding-left: 45px;
}
.menu {
	color: var(--color-secondary);
}
.menu__list {
	display: flex;
	align-items: center;
}
.menu__list-item {
	&:not(:last-child) {
		margin-right: 20px;
	}
}
.menu__list-link {
	font-family: Montserrat;
	font-style: normal;
	font-weight: 400;
	font-size: 20px;
	line-height: 17px;
	text-align: left;
	padding: 5px;
	border-bottom: 2px solid transparent;
	transition: border-bottom 0.3s ease;

	&:hover {
		border-bottom: 2px solid var(--color-secondary);
	}
}
